.auth__main {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: $primary;
    margin: 0;
    height: 100vh;
    width: 100vw;
}

.auth__box-container {
    background-color: #fff;
    box-shadow: 0 3px $dark-grey;
    border-radius: 2px;
    padding: 1.25rem;
    width: 250px;
}

.auth__title {
    color: darken($color: $primary, $amount: 20);
    margin-bottom: 1.25rem;
}

.auth__input {
    color: $dark-grey;
    border: 0;
    border-bottom: 1px solid $light-grey;
    margin-bottom: 10px;
    height: 20px;
    width: 100%;
    font-size: 16px;
    transition: border-bottom 0.3s ease;

    &:focus {
        border-bottom: 1px solid $primary;
        outline: none;
    }
}

.auth__social-network {
    align-items: center;
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding-top: 20px;
    padding-bottom: 20px;
    width: 100%;
}

.auth__alert-error {
    background-color: #f00;
    border-radius: 5px;
    color: #fff;
    display: flex;
    justify-content: center;
    margin-bottom: 10px;
    padding: 5px;
}
